<template>
	<view class="idle-publish-container">
		<!-- 自定义导航栏 -->
		<view class="nav-bar" :style="{ paddingTop: statusBarHeight + 'px' }">
			<view class="nav-content" :style="{ height: navBarHeight + 'px' }">
				<view class="back-btn" @tap="handleBack">
					<uni-icons type="left" size="20" color="#333333"></uni-icons>
				</view>
				<text class="nav-title">闲置发布</text>
				<view class="right-btn" @tap="handlePreview">
					<text class="preview-text">预览</text>
				</view>
			</view>
		</view>
		
		<!-- 占位元素 -->
		<view class="placeholder" :style="{ height: `calc(${statusBarHeight}px + ${navBarHeight}px)` }"></view>
		
		<!-- 表单内容 -->
		<scroll-view class="form-scroll" scroll-y>
			<!-- 闲置类型选择 -->
			<view class="form-section">
				<view class="section-title">闲置类型</view>
				<view class="type-grid">
					<view 
						v-for="(type, index) in idleTypes" 
						:key="index"
						class="type-item"
						:class="{ active: formData.type === type.value }"
						@tap="selectIdleType(type.value)"
					>
						<uni-icons :type="type.icon" size="24" :color="formData.type === type.value ? '#ffffff' : '#666666'"></uni-icons>
						<text class="type-text">{{ type.label }}</text>
					</view>
				</view>
			</view>
			
			<!-- 图片上传区域 -->
			<view class="form-section">
				<view class="upload-area">
					<view class="upload-grid">
						<view 
							v-for="(item, index) in images" 
							:key="index" 
							class="image-item"
						>
							<image class="preview-image" :src="item" mode="aspectFill"></image>
							<view class="delete-btn" @tap.stop="deleteImage(index)">
								<uni-icons type="closeempty" size="14" color="#ffffff"></uni-icons>
							</view>
						</view>
						
						<view class="upload-btn" @tap="chooseImage" v-if="images.length < 9">
							<uni-icons type="camera" size="24" color="#999999"></uni-icons>
							<text class="upload-text">{{images.length}}/9</text>
						</view>
					</view>
					<text class="tip-text">第一张图片将作为主图</text>
				</view>
			</view>
			
			<!-- 标题描述区域 -->
			<view class="form-section">
				<view class="input-item">
					<input 
						class="title-input" 
						v-model="formData.title" 
						placeholder="标题，品牌型号都是买家关注的" 
						maxlength="30"
					/>
					<text class="count-text">{{formData.title.length}}/30</text>
				</view>
				
				<view class="input-item">
					<textarea 
						class="desc-input" 
						v-model="formData.description" 
						placeholder="描述一下物品的细节和转手原因，更容易出售" 
						maxlength="1000"
						auto-height
					/>
					<text class="count-text">{{formData.description.length}}/1000</text>
				</view>
			</view>
			
			<!-- 价格区域 -->
			<view class="form-section">
				<view class="price-item">
					<text class="label">价格</text>
					<view class="price-input-wrapper">
						<text class="price-symbol">¥</text>
						<input 
							class="price-input" 
							v-model="formData.price" 
							type="digit" 
							placeholder="0.00" 
						/>
					</view>
				</view>
				
				<view class="price-item">
					<text class="label">原价</text>
					<view class="price-input-wrapper">
						<text class="price-symbol">¥</text>
						<input 
							class="price-input" 
							v-model="formData.originalPrice" 
							type="digit" 
							placeholder="0.00（选填）" 
						/>
					</view>
				</view>
			</view>
			
			<!-- 分类区域 -->
			<view class="form-section">
				<view class="select-item" @tap="showCategoryPicker">
					<text class="label">分类</text>
					<view class="select-value">
						<text class="value-text">{{formData.category || '请选择分类'}}</text>
						<uni-icons type="right" size="16" color="#999999"></uni-icons>
					</view>
				</view>
				
				<view class="select-item" @tap="showConditionPicker">
					<text class="label">成色</text>
					<view class="select-value">
						<text class="value-text">{{formData.condition || '请选择成色'}}</text>
						<uni-icons type="right" size="16" color="#999999"></uni-icons>
					</view>
				</view>
			</view>
			
			<!-- 交易区域 -->
			<view class="form-section">
				<view class="select-item" @tap="showLocationPicker">
					<text class="label">交易地点</text>
					<view class="select-value">
						<text class="value-text">{{formData.location || '请选择交易地点'}}</text>
						<uni-icons type="right" size="16" color="#999999"></uni-icons>
					</view>
				</view>
				
				<view class="select-item">
					<text class="label">交易方式</text>
					<view class="tag-group">
						<view 
							class="tag-item" 
							:class="{ active: formData.tradeMethod.includes('meet') }"
							@tap="toggleTradeMethod('meet')"
						>
							<text class="tag-text">线下交易</text>
						</view>
						<view 
							class="tag-item" 
							:class="{ active: formData.tradeMethod.includes('express') }"
							@tap="toggleTradeMethod('express')"
						>
							<text class="tag-text">快递邮寄</text>
						</view>
					</view>
				</view>
			</view>
			
			<!-- 底部安全区域 -->
			<view class="safe-area-bottom"></view>
		</scroll-view>
		
		<!-- 底部操作栏 -->
		<view class="bottom-bar">
			<view class="draft-btn" @tap="saveDraft">
				<text class="draft-text">存草稿</text>
			</view>
			<view class="publish-btn" @tap="publishItem">
				<text class="publish-text">立即发布</text>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				statusBarHeight: 0,
				navBarHeight: 44,
				images: [],
				formData: {
					type: 'digital', // 默认为数码产品
					title: '',
					description: '',
					price: '',
					originalPrice: '',
					condition: '几乎全新',
					location: '同城交易',
					contact: {
						name: '',
						phone: '',
						wechat: ''
					}
				},
				idleTypes: [
					{ label: '数码产品', value: 'digital', icon: 'phone' },
					{ label: '家居家电', value: 'home', icon: 'home' },
					{ label: '服饰鞋包', value: 'clothing', icon: 'shop' },
					{ label: '母婴用品', value: 'baby', icon: 'heart' },
					{ label: '美妆护肤', value: 'beauty', icon: 'star' },
					{ label: '运动户外', value: 'sports', icon: 'medal' },
					{ label: '图书音像', value: 'book', icon: 'paperplane' },
					{ label: '其他闲置', value: 'other', icon: 'more-filled' }
				],
				conditionOptions: ['全新未拆封', '几乎全新', '轻微使用痕迹', '明显使用痕迹', '功能正常']
			}
		},
		onLoad() {
			// 获取状态栏高度
			const systemInfo = uni.getSystemInfoSync()
			this.statusBarHeight = systemInfo.statusBarHeight
		},
		methods: {
			handleBack() {
				uni.navigateBack()
			},
			handlePreview() {
				uni.showToast({
					title: '预览功能开发中',
					icon: 'none'
				})
			},
			chooseImage() {
				uni.chooseImage({
					count: 9 - this.images.length,
					sizeType: ['compressed'],
					sourceType: ['album', 'camera'],
					success: (res) => {
						this.images = [...this.images, ...res.tempFilePaths]
					}
				})
			},
			deleteImage(index) {
				this.images.splice(index, 1)
			},
			showCategoryPicker() {
				uni.showActionSheet({
					itemList: this.categoryOptions,
					success: (res) => {
						this.formData.category = this.categoryOptions[res.tapIndex]
					}
				})
			},
			showConditionPicker() {
				uni.showActionSheet({
					itemList: this.conditionOptions,
					success: (res) => {
						this.formData.condition = this.conditionOptions[res.tapIndex]
					}
				})
			},
			showLocationPicker() {
				uni.navigateTo({
					url: '/pages/location/select/index',
					events: {
						// 监听选择位置后的回调
						locationSelected: (location) => {
							this.formData.location = location
						}
					}
				})
			},
			toggleTradeMethod(method) {
				const index = this.formData.tradeMethod.indexOf(method)
				if (index > -1) {
					// 至少保留一种交易方式
					if (this.formData.tradeMethod.length > 1) {
						this.formData.tradeMethod.splice(index, 1)
					}
				} else {
					this.formData.tradeMethod.push(method)
				}
			},
			selectIdleType(type) {
				this.formData.type = type
			},
			saveDraft() {
				// 检查必填项
				if (!this.validateForm(false)) return
				
				uni.showToast({
					title: '已保存到草稿箱',
					icon: 'success'
				})
				
				setTimeout(() => {
					uni.navigateBack()
				}, 1500)
			},
			publishItem() {
				// 检查必填项
				if (!this.validateForm(true)) return
				
				uni.showLoading({
					title: '发布中...'
				})
				
				// 模拟发布过程
				setTimeout(() => {
					uni.hideLoading()
					uni.showToast({
						title: '发布成功',
						icon: 'success'
					})
					
					setTimeout(() => {
						uni.navigateBack()
					}, 1500)
				}, 1500)
			},
			validateForm(isPublish) {
				// 图片验证
				if (this.images.length === 0) {
					uni.showToast({
						title: '请至少上传一张图片',
						icon: 'none'
					})
					return false
				}
				
				// 标题验证
				if (!this.formData.title.trim()) {
					uni.showToast({
						title: '请填写商品标题',
						icon: 'none'
					})
					return false
				}
				
				// 发布时的额外验证
				if (isPublish) {
					// 价格验证
					if (!this.formData.price) {
						uni.showToast({
							title: '请填写商品价格',
							icon: 'none'
						})
						return false
					}
					
					// 分类验证
					if (!this.formData.category) {
						uni.showToast({
							title: '请选择商品分类',
							icon: 'none'
						})
						return false
					}
					
					// 成色验证
					if (!this.formData.condition) {
						uni.showToast({
							title: '请选择商品成色',
							icon: 'none'
						})
						return false
					}
					
					// 交易地点验证
					if (!this.formData.location) {
						uni.showToast({
							title: '请选择交易地点',
							icon: 'none'
						})
						return false
					}
				}
				
				return true
			}
		}
	}
</script>

<style>
	.idle-publish-container {
		display: flex;
		flex-direction: column;
		height: 100vh;
		background-color: #f5f5f5;
	}
	
	.nav-bar {
		background-color: #ffffff;
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		z-index: 99;
		box-shadow: 0 2rpx 8rpx rgba(0, 0, 0, 0.04);
	}
	
	.nav-content {
		display: flex;
		align-items: center;
		position: relative;
	}
	
	.back-btn {
		width: 88rpx;
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	
	.nav-title {
		position: absolute;
		left: 50%;
		transform: translateX(-50%);
		font-size: 34rpx;
		font-weight: 500;
		color: #333333;
	}
	
	.right-btn {
		position: absolute;
		right: 30rpx;
		height: 100%;
		display: flex;
		align-items: center;
	}
	
	.preview-text {
		font-size: 28rpx;
		color: #666666;
	}
	
	.placeholder {
		width: 100%;
	}
	
	.form-scroll {
		flex: 1;
		overflow: hidden;
	}
	
	.form-section {
		background-color: #ffffff;
		margin-bottom: 20rpx;
		padding: 20rpx 30rpx;
	}
	
	.section-title {
		font-size: 30rpx;
		font-weight: 500;
		color: #333333;
		margin-bottom: 20rpx;
	}
	
	.type-grid {
		display: flex;
		flex-wrap: wrap;
		margin: 0 -10rpx;
	}
	
	.type-item {
		width: calc(33.33% - 20rpx);
		height: 100rpx;
		margin: 10rpx;
		border-radius: 8rpx;
		overflow: hidden;
		position: relative;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}
	
	.type-item.active {
		background-color: #fff2f0;
		border: 1rpx solid #fc3e2b;
	}
	
	.type-text {
		font-size: 26rpx;
		color: #666666;
	}
	
	.upload-area {
		width: 100%;
	}
	
	.upload-grid {
		display: flex;
		flex-wrap: wrap;
		margin: 0 -10rpx;
	}
	
	.image-item, .upload-btn {
		width: calc(33.33% - 20rpx);
		height: 200rpx;
		margin: 10rpx;
		border-radius: 8rpx;
		overflow: hidden;
		position: relative;
	}
	
	.preview-image {
		width: 100%;
		height: 100%;
	}
	
	.delete-btn {
		position: absolute;
		top: 10rpx;
		right: 10rpx;
		width: 40rpx;
		height: 40rpx;
		background-color: rgba(0, 0, 0, 0.5);
		border-radius: 50%;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	
	.upload-btn {
		background-color: #f8f8f8;
		border: 1rpx dashed #dddddd;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}
	
	.upload-text {
		font-size: 24rpx;
		color: #999999;
		margin-top: 10rpx;
	}
	
	.tip-text {
		font-size: 24rpx;
		color: #999999;
		margin-top: 20rpx;
	}
	
	.input-item {
		margin-bottom: 20rpx;
		position: relative;
	}
	
	.input-item:last-child {
		margin-bottom: 0;
	}
	
	.title-input {
		width: 100%;
		height: 80rpx;
		font-size: 30rpx;
		color: #333333;
	}
	
	.desc-input {
		width: 100%;
		min-height: 200rpx;
		font-size: 28rpx;
		color: #333333;
		line-height: 1.5;
	}
	
	.count-text {
		position: absolute;
		right: 0;
		bottom: 0;
		font-size: 24rpx;
		color: #999999;
	}
	
	.price-item {
		display: flex;
		align-items: center;
		margin-bottom: 20rpx;
	}
	
	.price-item:last-child {
		margin-bottom: 0;
	}
	
	.label {
		width: 160rpx;
		font-size: 30rpx;
		color: #333333;
	}
	
	.price-input-wrapper {
		flex: 1;
		display: flex;
		align-items: center;
	}
	
	.price-symbol {
		font-size: 30rpx;
		color: #333333;
		margin-right: 10rpx;
	}
	
	.price-input {
		flex: 1;
		height: 80rpx;
		font-size: 30rpx;
		color: #333333;
	}
	
	.select-item {
		display: flex;
		align-items: center;
		margin-bottom: 20rpx;
		min-height: 80rpx;
	}
	
	.select-item:last-child {
		margin-bottom: 0;
	}
	
	.select-value {
		flex: 1;
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
	
	.value-text {
		font-size: 30rpx;
		color: #333333;
	}
	
	.tag-group {
		flex: 1;
		display: flex;
	}
	
	.tag-item {
		padding: 10rpx 20rpx;
		background-color: #f5f5f5;
		border-radius: 8rpx;
		margin-right: 20rpx;
	}
	
	.tag-item.active {
		background-color: #fff2f0;
		border: 1rpx solid #fc3e2b;
	}
	
	.tag-text {
		font-size: 26rpx;
		color: #666666;
	}
	
	.tag-item.active .tag-text {
		color: #fc3e2b;
	}
	
	.safe-area-bottom {
		height: 120rpx;
	}
	
	.bottom-bar {
		position: fixed;
		left: 0;
		right: 0;
		bottom: 0;
		height: 100rpx;
		background-color: #ffffff;
		display: flex;
		align-items: center;
		padding-bottom: env(safe-area-inset-bottom);
		box-shadow: 0 -2rpx 8rpx rgba(0, 0, 0, 0.04);
		z-index: 99;
	}
	
	.draft-btn, .publish-btn {
		flex: 1;
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	
	.draft-text {
		font-size: 30rpx;
		color: #666666;
	}
	
	.publish-btn {
		background: linear-gradient(to right, #ff8c00, #ff4500);
	}
	
	.publish-text {
		font-size: 30rpx;
		color: #ffffff;
		font-weight: 500;
	}
</style> 